import React, { useEffect, useState } from 'react'
import { addZero } from '../time/time'
import rain from '../../resourse/audio/rain.mp3'

const Audio = props => {
    switch(props.name.toLowerCase()){
        case 'rain': return <audio src={rain} onPlay={e => e.target.volume = .1} ></audio>
        default: return <div>Undefined</div>
    }
}

const Timer = props => {
    let [isStart, setIsStart] = useState(false)
    const strokeArr = 1821
    let audio = document.querySelector('audio')
    
    useEffect(() => {
        if(props.time<=0) return setIsStart(false)
        if(isStart){
            setTimeout(() => {
                props.setTime(props.time - 1)
            }, 1000)
        }
    }, [isStart, props.time])
    useEffect(() => {
        if(audio){
            if(isStart){
                audio.play()
            }else{
                audio.pause()
            }
        }
    }, [isStart])

    

    return (
        <div className="timer">
            <div className="container">
                <div className="timer__body">
                    <div className="timer__circle">
                        <svg>
                            <circle r="290" cx="305" cy="305" style={{strokeDasharray:`${strokeArr}px`, strokeDashoffset: `${strokeArr/props.primary*(props.primary-props.time)}px`}}></circle>
                        </svg>
                        <div className="timer__count">
                            {`${addZero(props.time/3600)}:${addZero(props.time/60%60)}:${addZero(props.time%60)}`}
                        </div>
                        <div className="timer__song">{props.current}</div>
                        <Audio name={props.current} />
                    </div>
                    <div className="flex">
                        {isStart
                        ? <button onClick={() => setIsStart(false)} className="main__btn timer__btn">Stop</button>
                        : <button onClick={() => setIsStart(true)} className="main__btn timer__btn">Start</button>}
                        <button onClick={() => {setIsStart(false); setTimeout(() => {audio.currentTime = 0; props.setTime(props.primary); setIsStart(true)},500)}} className="main__btn timer__btn timer__btn--output">Reset</button>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Timer