import React from 'react'
import './footer.css'
import {NavLink, withRouter} from 'react-router-dom'

const Footer = props => {

    const onMusicPage = props.match.path === '/music'

    return (
        <footer className="footer">
            <div className="footer__progress">
                {onMusicPage
                ? <><span className="progress"></span><span></span></>
                : <><span className="completed"></span><span className="progress"></span></>}
            </div>
            <div className="container">
                <div className="footer__body">
                    <div className="footer__column">
                        <NavLink className={`footer__btn main__btn ${onMusicPage && 'disabled'}`} to="/music">
                            Previous
                        </NavLink>
                    </div>
                    <div className="footer__column">
                        {props.song || <span>Song...</span>}
                    </div>
                    <div className="footer__column">
                        {!onMusicPage ? <span>Time...</span> : null}
                    </div>
                    <div className="footer__column">
                        <NavLink onClick={(e) => !props.song && e.preventDefault()} className={`footer__btn main__btn ${!props.song && 'disabled'}`} to={onMusicPage ? '/time' : '/timer'}>
                            {onMusicPage ? 'Next step' : 'Start'}
                        </NavLink>
                    </div>
                </div>
            </div>
        </footer>
    )
}

export default withRouter(Footer)