import React from 'react'
import image from '../../resourse/img/music'

const Music = props => {

    // MAPS
    const filterList = props.list.filter(e => e.toLowerCase().indexOf(props.search.toLowerCase().trim()) !== -1)
    const list = filterList.map(e => <div key={e} onClick={() => props.setCurrent(e)} className={`music__column ${props.current === e ? 'music__current' : undefined}`}><div className="music__img">{image(e)}</div><div className="music__name">{e}</div></div>)

    return (
        <div className="music">
            <div className="container">
                <div className="music__body">
                    <div className="music__search">
                        <input value={props.search} onChange={e => props.setSearch(e.target.value)} type="text" className="music__input" placeholder="Search song..." />
                    </div>
                    <div className="music__list">
                        {list}
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Music