import React from 'react'

export const addZero = (num) => {
    num = Math.floor(num)
    return num < 10 ? '0'+num : num
}

const Time = props => {
    
    props.setPrimary(props.time)
    
    const changeNum = (obj) => {
        const check = (value, limit, index = 0) => {
            if(Math.floor(props.time)/value%(limit+1) >= limit && obj.rotate) return props.setTime(props.time - value*limit)
            if(Math.floor(props.time)/value%(limit+1) <= index && !obj.rotate) return props.setTime(props.time + value*limit)
            return props.setTime(obj.rotate ? props.time + value : props.time - value)
        }
        switch(obj.id){
            case 'second': return check(1, 59)
            case 'minute': return check(60, 59)
            case 'hour': return check(3600,99, 1)
        }
    }

    const Arrow = (obj) => {
        return (
            <button onClick={() => changeNum(obj)}>
                <svg style={{transform: `rotate(${obj.rotate || 0}deg)`}} width="67" height="53" viewBox="0 0 67 53" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M61.459 6.00006C59.7078 9.96323 41.1955 52.9053 32.1154 46.3474C18.5626 36.5593 14.5977 18.897 5.99969 6.00006" stroke={obj.color} strokeWidth="11" strokeLinecap="round" strokeLinejoin="round"/>
                </svg>
            </button>
        )
    }

    return (
        <div className="time">
            <div className="container">
                <div className="time__body">
                    <div className="time__column">
                        <Arrow id="hour" color="#40514E" rotate='180' />
                        {addZero(props.time/3600%100)}:
                        <Arrow id="hour" color="#40514E" />
                    </div>
                    <div className="time__column">
                        <Arrow id="minute" color="#40514E" rotate='180' />
                        {addZero(props.time/60%60)}:
                        <Arrow id="minute" color="#40514E" />
                    </div>
                    <div className="time__column">
                        <Arrow id="second" color="#11999E" rotate='180' />
                        {addZero(props.time%60)}
                        <Arrow id="second" color="#11999E" />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Time