import React from 'react'
import {NavLink} from 'react-router-dom'

const Main = props => {
    return (
        <div className="main">
            <div className="container">
                <div className="main__body">
                    <div className="main__title">
                        Take 3 steps to get started
                    </div>
                    <div className="main__row">
                        <div className="main__column">
                            <div className="main__step">Step 1</div>
                            <div className="main__text">Choose background music</div>
                        </div>
                        <div className="main__column">
                            <div className="main__step">Step 2</div>
                            <div className="main__text">Set a time</div>
                        </div>
                        <div className="main__column">
                            <div className="main__step">Step 3</div>
                            <div className="main__text">Get enjoy</div>
                        </div>
                    </div>
                    <NavLink to="/music" className="main__btn">
                        Get Started
                    </NavLink>
                </div>
            </div>
        </div>
    )
}

export default Main