import React from 'react'
import './header.css'
import {NavLink} from 'react-router-dom'

const Header = props => {
    return (
        <header className="header">
            <div className="container">
                <div className="header__body">
                    <NavLink to="/" className="header__logo">
                        <svg width="81" height="81" viewBox="0 0 81 81" fill="none" xmlns="http://www.w3.org/2000/svg" xlink="http://www.w3.org/1999/xlink"><rect width="81" height="81" fill="url(#pattern0)" /><defs><pattern id="pattern0" patternContentUnits="objectBoundingBox" width="1" height="1"><use href="#image0" transform="scale(0.005)" /></pattern><image id="image0" width="200" height="200" href="data:image/png;base64," /></defs></svg>
                    </NavLink>
                    <nav className="header__nav">
                        <ul className="header__list">
                            <li><NavLink to="/" className="header__item" >Home</NavLink></li>
                            <li><NavLink to="/settings" className="header__item" >Settings</NavLink></li>
                            <li><NavLink to="/contact" className="header__item" >Contact us</NavLink></li>
                        </ul>
                    </nav>
                </div>
            </div>
        </header>
    )
}

export default Header


