import React from 'react'
import './music.css'
import Music from './music'
import {connect} from 'react-redux'
import Footer from '../footer/footer'
import {setSearch, setCurrent} from '../../redux/main'

const MusicContainer = props => {
    return <>
        <Music {...props} />
        <Footer song={props.current} path={props.match.path} />
    </>
}

const mapState = (state) => ({
    list: state.main.list,
    search: state.main.search,
    current: state.main.current
})

export default connect(mapState, {setSearch, setCurrent})(MusicContainer)