import React from 'react'
import Timer from './timer'
import './timer.css'
import {connect} from 'react-redux'
import {setTime, setStroke} from '../../redux/main'

const TimerContainer = props => {
    return <Timer {...props} />
}

const mapState = (state) => ({
    time: state.main.time,
    primary: state.main.primaryTime,
    stroke: state.main.stroke,
    current: state.main.current
})

export default connect(mapState, {setTime, setStroke})(TimerContainer)