const initialState = {
    list: ['Rain', 'Nature', 'Wind', 'Fire', 'Beach', 'Street', 'Bus', 'White noise', 'Ocean', 'Forest at night', 'Royal library', 'Airplane'],
    search: '',
    current: '',
    time: 0,
    primaryTime: 0,
    stroke: 0
}

const main = (state = initialState, action) => {
    switch(action.type){
        case 'search': return {...state, search: action.value}
        case 'current': return {...state, current: action.value}
        case 'time': return {...state, time: action.value}
        case 'primary': return {...state, primaryTime: action.value}
        case 'stroke': return {...state, stroke: action.value}
        default: return state
    }
}

export const setSearch = value => ({type: 'search', value})
export const setCurrent = value => ({type: 'current', value})
export const setTime = value => ({type: 'time', value})
export const setPrimary = value => ({type: 'primary', value})
export const setStroke = value => ({type: 'stroke', value})

export default main